<template>
  <div class="print-inner">
    <h3 class="print-check-title">{{typeName}}申请单</h3>
    <patient-info :patient-data="patientData"/>
    <div class="print-info-box">
      <p>临床印象：</p>
      <p>标本：</p>
      <p>{{typeName}}内容：
        <span v-for="(item,index) in checkList" :key="index" v-show="item.checkFlag == checkFlag">
          {{item.detailItemName}}
        </span>
      </p>
      <p>备注：</p>
    </div>
      <div class="print-check-footer footer-top">
        <span>报告日期：</span>
        <span>检验者：</span>
        <span>接收时间：</span>
        <span style="width: 200px">标本拒收原因：</span>
      </div>
      <div class="print-check-footer">
        <span>开单时间：{{ creatDate }}</span>
        <span>医生：{{patientData.userName}}</span>
        <span>送检人：</span>
        <span style="width: 200px">标本收集时间：</span>
      </div>
  </div>
</template>

<script>
import { dateFmt } from '../../util/fun'
import patientInfo from '../../components/print-patientInfo'
import {
  getEmpBasicMainEntyTable,
  getYiCunCheckItemList
} from '../../api'
export default {
  components: {
    patientInfo
  },
  data () {
    return {
      patientName: '',
      patientSex: '',
      patientAge: '',
      deptName: '',
      registrationNum: '',
      patientData: {},
      checkList: [],
      checkFlag: 1,
    }
  },
  computed: {
    creatDate () {
      return dateFmt("yyyy-MM-dd hh:mm:ss",new Date())
    },
    typeName () {
      return this.checkFlag == '1' ? '检验' : '检查'
    }
  },
  methods: {
    getPatintData () {
      getEmpBasicMainEntyTable({ registrationNum: this.registrationNum }).then(res=> {
        if(res.data.code == '0'){
          this.patientData = res.data.data[0]
          this.patientData.emSex = this.patientData.emSex === '1' ? '男' : '女'
          this.getCheckData()
        }else{
          this.$message.error('获取患者信息失败！')
        }
      })
    },
    getCheckData () {
      getYiCunCheckItemList({ empBasicMainID: this.patientData.empBasicMainID }).then(res=>{
        if (res.data.code == '0') {
          this.checkList = res.data.data
        }else{
          this.$message.error('获取检测内容失败！')
        }
      })
    }
  },
  mounted () {
    let id= this.$route.query.id
    let flag = this.$route.query.flag
    if (id && flag) {
        this.checkFlag = flag
        this.registrationNum = id
       this.getPatintData()
    }else{
      this.$message.error('访问参数不正确！')
    }
  }
}
</script>

<style>
.print-inner{
  width: 960px;
  padding-left: 40px;
}
.print-check-title{
  padding: 24px 0;
  width: 100%;
  font-size: 26px;
  font-weight: normal;
  color: #000;
  text-align: center;
}
.print-info-box {
  padding: 0 10px;
}
.print-info-box p{
  margin: 20px 0;
}
.print-check-footer{
  display: flex;
  width: 100%;
  padding: 10px 0;
  justify-content:space-between;

}
.footer-top {
  border-bottom: 1px solid #000;
}
</style>



